import { TextItem, TextMarkedContent } from "pdfjs-dist/types/src/display/api"

export const getFilterObjByTransformHeight = (items: (TextItem | TextMarkedContent)[], height: number): TextItem[] => {
  const res = items.filter((item) => {
    if ('str' in item) {
      return item.transform[5] <= height && !item.hasEOL
    }
    return false
  })
  return res as TextItem[]
}