import { PDFDocumentProxy } from 'pdfjs-dist'
import { SetErrors } from '../../..'
import { FrameCheckConfig } from '../../../types'
import { FrameHeightsData } from '../../config/types'
import { checkFrame } from './helpers/checkFrame'

export const checkFrameService = async (
  pdfData: PDFDocumentProxy,
  setErrors: SetErrors,
  config: FrameCheckConfig
): Promise<FrameHeightsData> => {
  const { mainFramePageNum, smallField1, smallField2, smallField3, smallField4, ...mainConfig } = config

  const heightConfig: Record<string, number> = {}

  for (let i = mainFramePageNum; i <= pdfData.numPages; i++) {
    const isMain = i === mainFramePageNum
    const c = isMain ? mainConfig : { smallField1, smallField2, smallField3, smallField4 }

    const height = await checkFrame(pdfData, setErrors, c, i, isMain)
    heightConfig[i.toString()] = height
  }

  return heightConfig
}
