import { PDFDocumentProxy } from 'pdfjs-dist'
import { SetErrors } from '../../../..'
import { MainFrameCheckConfig, SmallFrameCheckConfig } from '../../../../types'
import {
  DEFAULT_FRAME_DATA,
  DEFAULT_FRAME_DATA_COORDS,
  FRAME_INDENT,
  FRAME_MAIN_HEIGHT,
  FRAME_SMALL_HEIGHT,
  FieldDataCoordsType,
  FrameType,
  fieldDataCoords,
} from '../../../config/constants/frame'
import { ERROR_TYPE, FRAME_ERROR_KEYS } from '../../../errors'
import { getFilterObjByTransformHeight } from '../../../utils/pdfFormaters/getFilterObjByTransformHeight'
import { getPageStringObj } from '../../../utils/pdfFormaters/getPageStringObj'
import { getCorrection } from './getCorrection'
import { getFrameHeight } from './getFrameHeight'
import { getFrameSizeConst } from './getFrameSizeConst'
import { getGroupName } from './getGroupName'

const pdfWidth = 210
const pdfHeight = 297

export const checkFrame = async (
  pdfData: PDFDocumentProxy,
  setErrors: SetErrors,
  config: MainFrameCheckConfig | SmallFrameCheckConfig,
  pageNumber: number,
  isMain: boolean
): Promise<number> => {
  const pageData = await getPageStringObj(pdfData, pageNumber)
  const pagesSize = pdfData.numPages
  const { kW, kH } = await getFrameSizeConst(pdfData, pageNumber, pdfWidth, pdfHeight)
  const defaultHeight = isMain ? FRAME_MAIN_HEIGHT : FRAME_SMALL_HEIGHT
  const stringDataFrame = getFilterObjByTransformHeight(pageData.items, (defaultHeight + 20) * kH) //      20 ,  20    
  const correction = getCorrection(
    stringDataFrame,
    isMain ? FrameType.main : FrameType.small,
    kW,
    kH,
    DEFAULT_FRAME_DATA_COORDS,
    DEFAULT_FRAME_DATA
  )

  Object.entries(config).map(([key, value]) => {
    if (!value) {
      return
    }
    const coordsFieldData = isMain
      ? fieldDataCoords.main[key as keyof FieldDataCoordsType['main']]
      : fieldDataCoords.small[key as keyof FieldDataCoordsType['small']]
    const fieldString = getGroupName({
      data: stringDataFrame,
      coordsFieldData,
      kW,
      kH,
      indent: FRAME_INDENT,
      correction,
    })

    if (!fieldString) {
      setErrors(ERROR_TYPE.frame, FRAME_ERROR_KEYS.rule3, pageNumber.toString(), key)
    }

    if (fieldString && typeof value === 'string' && fieldString !== value) {
      setErrors(ERROR_TYPE.frame, FRAME_ERROR_KEYS.rule4, pageNumber.toString(), key)
    }

    let isNumField = false
    if (key === 'maidField8' || key === 'smallField4') {
      isNumField = true
    }
    let isNumsField = false
    if (key === 'maidField9') {
      isNumsField = true
    }

    if (fieldString && isNumField && +fieldString !== pageNumber) {
      setErrors(ERROR_TYPE.frame, FRAME_ERROR_KEYS.rule1, pageNumber.toString())
    }

    if (fieldString && isNumsField && +fieldString !== pagesSize) {
      setErrors(ERROR_TYPE.frame, FRAME_ERROR_KEYS.rule2, pageNumber.toString(), pagesSize.toString())
    }
  })
  const height = getFrameHeight(defaultHeight, kH, correction.bottom)

  return height
}
